body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

td.title {
  padding-right: 15px;
  font-weight: bold;
}

.btn {
  cursor: pointer;
  border: 0px;
  border-radius: 0;
  padding: 5px 20px;
  border: lightgrey 1px solid;
  margin-bottom: 10px;
}

.btn:hover {
  background: #bfbfbf;
}

.btn:not(:last-child) {
  margin-right: 5px;
}

#root {
  padding: 40px 80px;
}

form {
  display: flex;
  flex-direction: column;
}

input, textarea {
  padding: 5px;
  border: lightgrey 1px solid;
  margin-bottom: 10px;
  width: 400px;
  box-sizing: border-box;
  font-family: "Helvetica Neue", sans-serif;
}

input:focus, textarea:focus {
  outline: none;
}

.overlay {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #00000059;
  z-index: 1;
  position: fixed;
}

.popup {
  z-index: 2;
  background: #FFF;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  top: 50px;
  padding: 20px 20px 30px;
}

.users {
  padding: 20px 40px;
  border: 1px solid lightgrey;
}

.users__item {
  padding: 10px 20px;
  border: 1px solid lightgrey;
  margin-bottom: 10px;
}

.posts {
  padding: 20px 40px;
  border: 1px solid lightgrey;
}

.posts__item {
  padding: 10px 20px;
  border: 1px solid lightgrey;
  margin-bottom: 10px;
}

.post {
  padding: 20px 40px;
  border: 1px solid lightgrey;
}

.comments {
  border: 1px solid lightgrey;
  padding: 20px 40px;
  margin-bottom: 20px;
}

.comments__item {
  padding: 10px 20px;
  border: 1px solid lightgrey;
  margin-bottom: 10px;
}

@media (max-width: 768px) {

  input, textarea {
    width: 100%;
  }

  #root {
    padding: 0;
  }
  .btn {
    width: 100%;
    margin-bottom: 10px;
  }
  .comments {
    padding: 10px;
  }
  .comments__item {
    padding: 10px;
  }
}